* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Epilogue, sans-serif;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.header-section {
  background: #141414;
}

.header-container {
  width: 100%;
  flex-direction: row;
  display: flex;
}

.logo-container {
  flex-direction: row;
  gap: 4px;
  margin-left: 25px;
  padding-top: 29px;
  display: flex;
}

.logo-header {
  width: 14px;
  height: 14px;
  color: #fff;
}

.logo-text {
  width: 80px;
  height: 10px;
  color: #fff;
  font-family: Epilogue;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.menu-burger {
  width: 24px;
  height: 24px;
  color: #fff;
}

.menu-button {
  cursor: pointer;
  height: 0;
  background: none;
  border: none;
  margin-bottom: 30px;
  margin-left: 205px;
  padding-top: 29px;
}

.menu-burger:hover, .menu-burger:focus {
  color: #817e7e;
}

.close-menu {
  width: 24px;
  height: 24px;
  color: #fff;
}

.close-menu:hover, .close-menu:focus {
  border-radius: 50%;
  box-shadow: 1px 4px 6px rgba(0, 0, 0, .16), 0 4px 4px rgba(0, 0, 0, .06), 0 1px 1px rgba(0, 0, 0, .12);
}

.js-menu-container {
  display: none;
}

.js-menu-container.is-open {
  z-index: 5;
  width: 100%;
  height: 100%;
  background-color: #1e2827;
  padding: 25px;
  display: flex;
  position: fixed;
  top: 0;
  right: 0;
}

.menu-toggle {
  cursor: pointer;
  width: 40px;
  height: 40px;
  cursor: pointer;
  background: none;
  border: none;
  flex-shrink: 0;
  justify-content: center;
  margin-top: 12px;
  margin-right: 10px;
  padding: 5px;
  display: flex;
  position: absolute;
  top: 10px;
  right: 15px;
}

.mobile-backdrop .mobile-navigation a {
  color: #fff;
  letter-spacing: -.28px;
  text-transform: uppercase;
  font-family: Epilogue;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
}

.mobile-list {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 14px;
  margin-top: 180px;
  margin-left: 30px;
  display: flex;
}

.anchor:hover, .anchor:focus {
  color: #1e3a1e;
}

.mobile-list li a:hover {
  color: #36bdaf;
}

.logo-text-backdrop {
  width: 80px;
  height: 10px;
  color: #fff;
  font-family: Epilogue;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.logo {
  margin-top: 5px;
}

.logo-header-backdrop {
  width: 14px;
  height: 14px;
  color: #fff;
}

.btn-phone, .navigation-list {
  display: none;
}

@media screen and (min-width: 768px) {
  .menu-button {
    display: none;
  }

  .header-container {
    flex-direction: row;
    gap: 80px;
    display: flex;
  }

  .navigation-list {
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 40px;
    margin-top: 29px;
    display: flex;
  }

  .nav a {
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-family: Epilogue;
    font-size: 9px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
  }

  .logo-header {
    width: 14px;
    height: 14px;
    color: #fff;
  }

  .logo-text {
    width: 80px;
    height: 10px;
    color: #fff;
    font-family: Epilogue;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
  }

  .btn-phone {
    width: 159px;
    height: 48px;
    background: var(--dark-green, #1e2827);
    cursor: pointer;
    border: none;
    border-radius: 8px;
    flex-shrink: 0;
    margin-top: 15px;
    margin-bottom: 20px;
    margin-left: 120px;
    display: block;
  }

  .btn-phone:focus, .btn-phone:hover {
    box-shadow: 1px 4px 6px rgba(0, 0, 0, .16), 0 4px 4px rgba(0, 0, 0, .06), 0 1px 1px rgba(0, 0, 0, .12);
  }

  .phone-number {
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-family: Epilogue;
    font-size: 9px;
    font-style: normal;
    font-weight: 400;
    line-height: 12px;
  }
}

@media screen and (min-width: 1280px) {
  .menu-button {
    display: none;
  }

  .logo-container {
    margin-left: 132px;
  }

  .btn-phone {
    margin-left: 575px;
  }

  .btn-phone:focus, .btn-phone:hover {
    box-shadow: 1px 4px 6px rgba(0, 0, 0, .16), 0 4px 4px rgba(0, 0, 0, .06), 0 1px 1px rgba(0, 0, 0, .12);
