  color: #fff;
  border: 1px solid #fff;
}

.swiper .swiper-pagination {
  position: static;
}

.swiper .swiper-pagination-fraction {
  letter-spacing: -.02em;
  color: #fff;
  padding: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.29;
}

.swiper .swiper-pagination-fraction:not(:lang(ae)):not(:lang(ar)):not(:lang(arc)):not(:lang(bcc)):not(:lang(bqi)):not(:lang(ckb)):not(:lang(dv)):not(:lang(fa)):not(:lang(glk)):not(:lang(he)):not(:lang(ku)):not(:lang(mzn)):not(:lang(nqo)):not(:lang(pnb)):not(:lang(ps)):not(:lang(sd)):not(:lang(ug)):not(:lang(ur)):not(:lang(yi)) {
